@import url(https://fonts.googleapis.com/css2?family=MedievalSharp&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  background-color: #282c34;
  text-align: center;
  width: 100vw;
}

.header img {
  height: 50px;
}

.newsletter {
  justify-content: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 0 auto;
}

.epicfont {
  font-family: 'MedievalSharp', cursive;
  font-weight: bold;
  font-size: 1.2em;
  color: #282c34;
}

form {
  display: inline-block;
  flex-direction: row;
  margin: 0;
  padding: 0;
}
.inline {
  display: inline-block;
}
input {
  padding: 14px 5px 11px 10px;
  font-family: 'Lato', sans-serif;
  font-size: 13px;
  text-decoration: none;
  width: 50%;
  background-color: transparent;
  border: solid 1px #466468;
  box-shadow: 0px 4px #466468;
}
.news-button {
  padding: 10px;
  width: 40%;
  margin-top: 0px;
  margin-left: 4px;
}

input:focus,
button:focus {
  outline: none;
}
/* Button style from card.css */

.card {
  max-width: 500px;
  margin: 0 auto;
}
.animate {
  transition: all 0.1s;
  -webkit-transition: all 0.1s;
}
.action-button {
  padding: 10px 40px;
  border-radius: 3px;
  font-family: 'Lato', sans-serif;
  font-size: 18px;
  color: #fff;
  text-decoration: none;
  margin-top: 25px;
}
.color {
  background-color: #587b7f;
  border-bottom: 5px solid #466468;
  text-shadow: 0px -2px #466468;
}
.action-button:active {
  transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
  border-bottom: 1px solid;
}

body {
  background-color: #fce6bd;
  background-image: url(/lotr-app/static/media/backgroud.5b6ecd6e.jpg);
  min-height: calc(100vh - 100px);
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #282c34;
  text-align: center;
  position: relative;
  padding-bottom: 100px; /* height of your footer */
}

.App {
  justify-content: center;
}

